import { useState } from 'react';
import PropTypes from 'prop-types';

import AddFormInstrumentView from './AddFormInstrumentView';

const AddFormInstrument = ({ onAdd, visible }) => {
  const [data, setData] = useState({
    name: '',
    price: '',
    diameter: '',
    height: '',
    tooth: ''
  });

  const onValueChange = (e) => {
    setData({ ...data, [e.target.name]: e.target.value });
  };

  const onSubmit = (event) => {
    event.preventDefault();
    onAdd(data);
    setData({
      name: '',
      price: '',
      diameter: '',
      height: '',
      tooth: ''
    });
  };

  const {
    name, price, diameter, height, tooth
  } = data;

  const inputs = [
    {
      label: ' : ', type: 'text', prop: 'name', value: name
    },
    {
      label: ' : ', type: 'number', prop: 'diameter', value: diameter
    },
    {
      label: ' : ', type: 'number', prop: 'height', value: height
    },
    {
      label: '  : ', type: 'number', prop: 'tooth', value: tooth
    },
    {
      label: ' ($): ', type: 'number', prop: 'price', value: price
    },
  ];

  return (
    <AddFormInstrumentView
      onSubmit={onSubmit}
      visible={visible}
      inputs={inputs}
      onValueChange={onValueChange}
    />
  );
};

AddFormInstrument.propTypes = {
  visible: PropTypes.bool.isRequired,
  onAdd: PropTypes.func.isRequired
};

export default AddFormInstrument;
